import { Injectable } from "@angular/core";
import { HttpClient } from '@angular/common/http';
import { Subject } from 'rxjs';
import { environment } from 'src/environments/environment.prod';
const url = environment.Server_Url+'ui/';
@Injectable({providedIn: 'root'})
export class UiProductsServices {
    
    updatedCartQuantity = new Subject<{cartQuantity: number}>();
    updatedSearchQuery = new Subject<{searchquery: string}>();
    constructor(private http: HttpClient) {}
    searchQueryListener() {
        return this.updatedSearchQuery.asObservable();
    }
    cartQuantityListener() {
        return this.updatedCartQuantity.asObservable();
    }
    cat(cat) {
       return this.http.get<{cats: object[]}>(url+'products/'+cat);
    }
    getProducts(pg, limit, order) {
        const skip = limit*pg - limit;
        return this.http.get<{items: Object[], count: Number, cart: any}>(url+"products/getAll/"+skip+"/"+limit+"/"+order);
    }
    filterize(object) {
        return this.http.post<{filteredItems: Object[], count: Number}>(url+"products/filter", object);
    }
    getProduct(id) {
        return this.http.get<{details: Object}>(url+"products/getProduct/"+id);
    }
    addReview(rev) {
        return this.http.post<{details: Object, count: number}>(url+"reviews/add", rev);
    }
    getReviews(id, pg, limit) {
        let skip = limit*pg - limit;
        return this.http.get<{reviews: Object, count: number}>(url+"reviews/get/"+id+"/"+skip+"/"+limit);
    }
    getProductRanking(id) {
        return this.http.get<{ratings:[{_id: number, count: number}], averageRate: Number}>(url+"products/rating/"+id);
    }
    pushToCart(product) {
        return this.http.post<{updatedCart: any, count: number,cartcount: number}>(url+"cart/add",product);
    }
    getCart(id) {
        return this.http.get<{updatedCart, count}>(url+"cart/get/"+id);
    }
    updateCart(id, qnt) {
        const body = {id, qnt};
        return this.http.patch<{updatedCart, count}>(url+"cart/update", body);
    }
    getTopLiked() {
        return this.http.get<{topLiked}>(url+"cart/top");
    }
}