import { Component, OnInit } from '@angular/core';
import { AdminServices } from '../admin-service';
import { PageEvent } from '@angular/material';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-products-list',
  templateUrl: './products-list.component.html',
  styleUrls: ['./products-list.component.css']
})
export class ProductsListComponent implements OnInit {
products: any;
productsCount: Number;
isLoading: Boolean = false;
postsPerPage: Number = 2;
pageSizeOptions = [1,10,20,30];
pageLength: Number;
currentPage: Number = 1;
notfound: Boolean = false;

  constructor(private productsServices: AdminServices) { }

  ngOnInit() {
    this.isLoading = true;
    this.productsServices.getProducts(this.currentPage, this.postsPerPage).subscribe(res => {
      this.products = res.items;
      this.productsCount = res.count;
      this.isLoading = false;
    })
  }
  delete(id) {
    this.productsServices.deleteItem(id).subscribe(del=>{
      const ind = this.products.findIndex(prod => {
        return prod._id === id
      });
        this.products = [...this.products].filter((value, index, arr) => {
          return index !== ind;
        });
        if(del) {
        this.productsServices.getProducts(this.currentPage, this.postsPerPage).subscribe(res => {
          this.products = res.items;
          this.productsCount = res.count;
          });
      }else {
        alert("error: Not deleted");
      }
   });
  }
  pageChange(pageData: PageEvent) {
    console.log(pageData);
    this.isLoading = true;
    this.currentPage = pageData.pageIndex + 1;
    this.postsPerPage = pageData.pageSize;
    this.productsServices.getProducts(this.currentPage, this.postsPerPage).subscribe(res => {
          this.products = res.items;
          this.productsCount = res.count;
          this.isLoading = false;
          });
  }
  adminsearch(itemquery) {
    const q = itemquery.value.search;
    this.currentPage = 1;
    this.isLoading = true;
    this.productsServices.searchquery(q).subscribe(res => { 
      if(res.items == null) {
        this.products = null;
        this.notfound = true;
        this.isLoading = false;
      }
      this.products = res.items;
      this.isLoading = false;
      }, err => {
        console.log(err);
        this.products = null;
        this.notfound = true;
        this.isLoading = false;
      });
  }
}
