import { UsersServices } from '../users.servicses';
import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { UniqueUsername } from '../common/custom-validators/uniqueUsername.validator';
@Component({
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  constructor( private usersServices: UsersServices) {}
  register: FormGroup;
  ngOnInit() {
    this.register = new FormGroup({
      username: new FormControl(null, {validators: [Validators.required], asyncValidators: [UniqueUsername.checkUsername]}),
      password: new FormControl(null, {validators: [Validators.required]}),
      
    });
  }
  login() {
      if(this.register.valid) {
        console.log(this.register.valid);
        this.usersServices.login(this.register.get('username').value, this.register.get('password').value,0);
      }
   }
}
