import { Component, OnInit } from '@angular/core';
import { AdminServices } from '../admin-service';
import { PageEvent } from '@angular/material';

@Component({
  selector: 'app-users-control',
  templateUrl: './users-control.component.html',
  styleUrls: ['./users-control.component.css']
})
export class UsersControlComponent implements OnInit {
  users: any;
  productsCount: Number;
  isLoading: Boolean = false;
  postsPerPage: Number = 6;
  pageSizeOptions = [1,10,20,30];
  pageLength: Number;
  currentPage: Number = 1;
  notfound: Boolean = false;
  password: string;
  currentid: string;

  constructor(private adminservices: AdminServices) { }

  ngOnInit() {
    this.isLoading = true;
    this.adminservices.getUsers(this.currentPage, this.postsPerPage).subscribe(res=>{
      this.users = res.users;
      this.isLoading = false;
    });
  }
  delete(id) {
    this.adminservices.deleteUser(id).subscribe(del=>{
      const ind = this.users.findIndex(prod => {
        return prod._id === id
      });
        this.users = [...this.users].filter((value, index, arr) => {
          return index !== ind;
        });
        if(del) {
        this.adminservices.getUsers(this.currentPage, this.postsPerPage).subscribe(res => {
          this.users = res.users;
          this.productsCount = res.count;
          });
      }else {
        alert("error: Not deleted");
      }
   });
  }
  pageChange(pageData: PageEvent) {
    this.isLoading = true;
    this.currentPage = pageData.pageIndex + 1;
    this.postsPerPage = pageData.pageSize;
    this.adminservices.getUsers(this.currentPage, this.postsPerPage).subscribe(res => {
          this.users = res.users;
          this.productsCount = res.count;
          this.isLoading = false;
          });
  }
  adminsearch(itemquery) {
    const q = itemquery.value.search;
    this.currentPage = 1;
    this.isLoading = true;
    this.adminservices.searchqueryUsers(q).subscribe(res => { 
      if(res.users == null) {
        this.users = null;
        this.notfound = true;
        this.isLoading = false;
      }
      this.users = res.users;
      this.isLoading = false;
      }, err => {
        this.users = null;
        this.notfound = true;
        this.isLoading = false;
      });
  }
  popModal(id) {
    this.currentid = id;
  }
  editRole(pass) {
    if(pass.value === localStorage.getItem('password')) {
    this.isLoading = true;
    this.adminservices.updateRole(this.currentid).subscribe(res=>{
      this.users = res.users;
      this.isLoading = false;
      });
    }
  }
}
