import { Component, OnInit, AfterContentInit } from '@angular/core';
import { UiProductsServices } from '../uiProductsServices.service';
import { ActivatedRoute, ParamMap, Router } from '@angular/router';
declare var $: any;

@Component({
  selector: 'app-product',
  templateUrl: './product.component.html',
  styleUrls: ['./product.component.css']
})
export class ProductComponent implements OnInit {
  product: Object;
  reviews: Object;
  id;
  revCount: number;
  currentPg: number = 1;
  averageRate: any;
  added: boolean;
  star1 = 0;
  star2 = 0;
  star3 = 0;
  star4 = 0;
  star5 = 0;
  sum1;
  sum2;
  sum3;
  sum4;
  sum5;

  constructor(private uiProductsServices: UiProductsServices, private route: ActivatedRoute, private router: Router) { }
  ngOnInit() {
    this.route.paramMap.subscribe((paramMap: ParamMap)=>{
      this.id = paramMap.get('id');
      
    this.uiProductsServices.getProduct(this.id).subscribe(product => {
      this.product = product.details;
        });
      });
      this.uiProductsServices.getCart(null).subscribe(res=>{
        res.updatedCart.map(item=>item.proid===this.id?this.added=true:this.added=false)
      });
    this.afterReview();
        // Product img zoom
    var zoomMainProduct = document.getElementsByClassName('.preview-pic');
    if (zoomMainProduct) {
      $('.preview-pic').zoom();
      }
     }
    publish(rev) {
      if(rev.valid) {
      let post = rev.value;
      post.productid = this.id;
      post.date = Date.now();
      this.uiProductsServices.addReview(post).subscribe(r => {
        this.reviews[0] = r.details;
      rev.value.name = null;
      rev.value.comment = null;
      this.afterReview();
      });
    }
  }
  goto(comming) {
    let nops = Math.ceil(this.revCount/5);
    if(nops === this.currentPg && comming==="next") {return;}
    if(this.currentPg === 1 && comming === "prev") {return;}
    if(comming === 'next') {this.currentPg = this.currentPg + 1;}
    else if(comming === 'prev') {this.currentPg = this.currentPg - 1;}
    this.uiProductsServices.getReviews(this.id, this.currentPg, 5).subscribe(res => {
      this.reviews = res.reviews;
      this.revCount = res.count;
      }); 
  }
  afterReview() {
    this.uiProductsServices.getReviews(this.id, this.currentPg, 5).subscribe(revs => {
      this.reviews = revs.reviews;
      this.revCount = revs.count;
    });
    this.uiProductsServices.getProductRanking(this.id).subscribe(res => {
      let total = 0;
      this.averageRate = (res.averageRate).toPrecision(2);
      for(let rate of res.ratings) {
        total += rate.count;
        let num = rate._id;
        this['star'+num] = rate.count;
      }
      this.sum1 = (this.star1)/total*100+'%';
      this.sum2 = (this.star2)/total*100+'%';
      this.sum3 = (this.star3)/total*100+'%';
      this.sum4 = (this.star4)/total*100+'%';
      this.sum5 = (this.star5)/total*100+'%';
    });
  }
  addToCart(product, qnt) {
    product.quantity = +qnt.value;
    console.log(product);
    this.uiProductsServices.pushToCart(product).subscribe(res=>{
      if(res.cartcount ===0) {
        this.added = true;
      } else {
        this.added = false;
      }
    })
  }
}

