import { UsersServices } from '../users.servicses';
import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { UniqueUsername } from '../common/custom-validators/uniqueUsername.validator';
@Component({
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.css']
})
export class SignupComponent implements OnInit {
  constructor(private userservices: UsersServices, private usersServices: UsersServices) {}
  register: FormGroup;
  ngOnInit() {
    this.register = new FormGroup({
      username: new FormControl(null, {validators: [Validators.required], asyncValidators: [UniqueUsername.checkUsername]}),
      password: new FormControl(null, {validators: [Validators.required]}),
      confirm: new FormControl(null, {validators: [Validators.required]}),
      
    });
  }
  signUp() {
    if(this.register.valid&&(this.register.get('password').value === this.register.get('confirm').value)) {
      console.log(this.register.valid);
      this.usersServices.add(this.register.get('username').value, this.register.get('password').value);
    }
  }
  
}
