import { Component, OnInit, ɵConsole } from '@angular/core';
import { UiProductsServices } from '../uiProductsServices.service';
import { RouterState, RouterLink, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-cart',
  templateUrl: './cart.component.html',
  styleUrls: ['./cart.component.css']
})
export class CartComponent implements OnInit {
  cartItems: any;
  isLoading = false;
  isLoading2 = false;
  id: string;
  constructor(private uiservices: UiProductsServices, private route: ActivatedRoute) { }

  ngOnInit() {
      const id = this.route.paramMap.subscribe(pm => {
        if(pm.has('id')) {this.id = pm.get('id')}
      });

      console.log(id);
      this.isLoading = true;
      this.uiservices.getCart(this.id).subscribe(res=>{
      this.cartItems = res.updatedCart;
      this.uiservices.updatedCartQuantity.next({cartQuantity: res.count});
      console.log(this.cartItems);
      this.isLoading = false;
    });
    this.isLoading = true;
    this.uiservices.getCart(this.id).subscribe(res=>{
      this.cartItems = res.updatedCart;
      this.uiservices.updatedCartQuantity.next({cartQuantity: res.count});
      console.log(this.cartItems);
      this.isLoading = false;
    });
  }
  delete(item) {
    const post = {_id:item.proid, brand: item.brand,newPrice:item.price,imagePath1:item.image,quantity:1};
    this.isLoading2 = true;
    this.uiservices.pushToCart(post).subscribe(res=>{
      this.uiservices.getCart(this.id).subscribe(res=>{
        this.cartItems = res.updatedCart;
        this.uiservices.updatedCartQuantity.next({cartQuantity: res.count});
        this.isLoading2= false;
      });
    });
  }
  total() {
    let total = 0;
    for(let item of this.cartItems) {
      total += item.quantity*item.price;
    }
    return total;
  }
  update(item, event) {
      this.uiservices.updateCart(item.proid, event.target.value).subscribe(res=>{
        console.log( res);
            this.cartItems = res.updatedCart;
          });
    }
}
