import { Component, OnInit } from '@angular/core';
import { Subscription, Subject } from 'rxjs';
import { UiProductsServices } from '../uiProductsServices.service';
import { Router } from '@angular/router';
import { UsersServices } from '../users/users.servicses';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {
  constructor(private uiProductsServices: UiProductsServices, private usersServices: UsersServices, private router: Router) { }
  isAuthSub: Subscription;
  isAdminSub: Subscription;
  cartQuantity: Subscription;
  isAuth: boolean;
  isAdmin: boolean;
  quantity: any = 0;
  cartClass;
  cartItems: any;
  id;
  username: string = localStorage.getItem('username');
  ngOnInit() {
    this.isAuthSub = this.usersServices.authStatListener().subscribe(update => {
      this.isAuth = update;
      if(!update) {this.username = 'user';this.cartItems=null;this.quantity=0}
    });
    this.isAdminSub = this.usersServices.adminStatListener().subscribe(update => {
      this.isAdmin = update;
      if(!update) {this.username = 'user';this.cartItems=null;this.quantity=0}
    });

    setTimeout(()=>{
      this.cartQuantity = this.uiProductsServices.cartQuantityListener().subscribe(res=>{
        if(res.cartQuantity > this.quantity) {
          this.cartClass = '30px';
          setTimeout(()=>{
            this.cartClass='';
          },1000);
          }
      this.quantity = res.cartQuantity;
    });
    },300);
  }

  uisearch(query) {
    if(query.value.searchquery) {
    this.router.navigate(['/categories']);   
  setTimeout(()=>{
    this.uiProductsServices.updatedSearchQuery.next({searchquery: query.value.searchquery});
},250);
    }
  }
  logout() {
    this.usersServices.logout();
    this.router.navigate(['/login']);
  }
  getCartItems() {
    if(this.isAuth) {
    this.uiProductsServices.getCart(this.id).subscribe(res=>{
      this.cartItems = res.updatedCart;
      });
    }
  }
  
}
