import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ProductComponent } from './product/product.component';
import { MainComponent } from './main/main.component';
import { CategoriesComponent } from './categories/categories.component';
import { CartComponent } from './cart/cart.component';
import { AddItemComponent } from './admin/add-item/add-item.component';
import { ProductsListComponent } from './admin/products-list/products-list.component';
import { SignupComponent } from './users/signup/signup.component';
import { AuthGuard } from './users/auth.guard';
import { LoginComponent } from './users/login/login.component';
import { AdminGuard } from './users/admin.guard';
import { UsersControlComponent } from './admin/users-control/users-control.component';

const routes: Routes = [
  {path: "" ,component: MainComponent},
  {path: "product/:id" ,component: ProductComponent},
  {path: "register" ,component: SignupComponent},
  {path: "login" ,component: LoginComponent},
  {path: "cart" ,component: CartComponent, canActivate: [AuthGuard]},
  {path: "cart/:id" ,component: CartComponent, canActivate: [AdminGuard]},
  {path: "addItem" ,component: AddItemComponent, canActivate:[AdminGuard]},
  {path: "editItem/:id" ,component: AddItemComponent, canActivate:[AdminGuard]},
  {path: "productsList" ,component: ProductsListComponent, canActivate:[AdminGuard]},
  {path: "users" ,component: UsersControlComponent, canActivate:[AdminGuard]},
  {path: 'categs', loadChildren: './categories/categories.module#CategoriesModule'},
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
  providers: [AuthGuard,AdminGuard]
})
export class AppRoutingModule { }
