import { Component, OnInit, HostListener, OnChanges } from '@angular/core';
import { FormGroup, FormControl, Validators, Form } from '@angular/forms';
import { mimeType } from './mimetype.validator';
import { AdminServices } from '../admin-service';
import { HttpEventType, HttpResponse } from '@angular/common/http';
import { ActivatedRoute, ParamMap } from '@angular/router';
import { UiProductsServices } from 'src/app/uiProductsServices.service';
import { FileDetector } from 'protractor';
declare var $: any;

@Component({
  selector: 'app-add-item',
  templateUrl: './add-item.component.html',
  styleUrls: ['./add-item.component.css']
})
export class AddItemComponent implements OnInit, OnChanges {
  form: FormGroup;
  imgPreview1: String | ArrayBuffer;
  imgPreview2: String | ArrayBuffer;
  imgPreview3: String | ArrayBuffer;
  progress: string ;
  uploadStatus: string;
  mode: string;
  id: string;
  post: any;
  but: string;
  brandlist = [];
  designlist = [];
  typelist = [];
  colorlist = [];
  sizelist = [];

  constructor(private uiProductsServices: UiProductsServices, private productsServices: AdminServices, private route: ActivatedRoute) {}

  ngOnInit() {
    this.form = new FormGroup({
      brand: new FormControl(null , { validators: [Validators.required]} ),
      color: new FormControl(null , { validators: [Validators.required]} ),
      gender: new FormControl(null, { validators: [Validators.required] }),
      design: new FormControl(null, { validators: [Validators.required] }),
      type: new FormControl(null, { validators: [Validators.required] }),
      oldPrice: new FormControl(null, { validators: [Validators.required] }),
      newPrice: new FormControl(null, { validators: [Validators.required] }),
      quantity: new FormControl(null, { validators: [Validators.required] }),
      size: new FormControl(null, { validators: [Validators.required] }),
      description: new FormControl(null, { validators: [Validators.required] }),
      photo1: new FormControl(null, { validators: [Validators.required], asyncValidators: [mimeType] }),
      photo2: new FormControl(null, { validators: [Validators.required], asyncValidators: [mimeType] }),
      photo3: new FormControl(null, { validators: [Validators.required], asyncValidators: [mimeType] }),
    });
    this.route.paramMap.subscribe((paramMap: ParamMap) => {
      if(paramMap.has('id')) {
        this.mode = 'edit';
        this.id = paramMap.get('id');
        this.productsServices.getOne(this.id).subscribe(res => {
          this.post = res.item;
          console.log(this.post);
          this.imgPreview1 = this.post.imagePath1;
          this.imgPreview2 = this.post.imagePath2;
          this.imgPreview3 = this.post.imagePath3;
          this.form.setValue({
            brand: this.post.brand,
            color: this.post.color,
            gender: this.post.gender,
            design: this.post.design,
            type: this.post.type,
            oldPrice: this.post.oldPrice,
            newPrice: this.post.newPrice,
            quantity: this.post.quantity,
            size: this.post.size,
            description: this.post.description,
            photo1: this.post.imagePath1,
            photo2: this.post.imagePath2,
            photo3: this.post.imagePath3
          });
        });
      }else {
        this.mode = 'add';
        this.id = null;
      }
    });
    
    this.uiProductsServices.cat('brand').subscribe(res => {
      this.brandlist = res.cats;
    });
    this.uiProductsServices.cat('design').subscribe(res => {
      this.designlist = res.cats;
    });
    this.uiProductsServices.cat('type').subscribe(res => {
      this.typelist = res.cats;
    });
    this.uiProductsServices.cat('color').subscribe(res => {
      this.colorlist = res.cats;
    });
    this.uiProductsServices.cat('size').subscribe(res => {
      this.sizelist = res.cats;
    });
  }

  ngOnChanges() {
    $('.progress-bar').css("width", this.progress);
  }
  upload() {
    if(this.form.valid) {
    let func;
    const body = this.form.value;
    body.brand.toLowerCase();
    body.type.toLowerCase();
    body.design.toLowerCase();
    body.color.toLowerCase();
    body.size.toLowerCase();
    if(this.mode === "add") {
     func = this.productsServices.uploadItem(body);
    }
    else if(this.mode === 'edit') {
      this.form.value.id = this.id;
      func = this.productsServices.editItem(body);
   }
   
    func.subscribe(event=> {
    if (event.type === HttpEventType.UploadProgress) {
        const percentDone = Math.round(100 * event.loaded / event.total);
        this.progress = percentDone+"%";
        this.uploadStatus = "Uploading..";
      } else if (event instanceof HttpResponse) {
        this.progress = "100%";
        setTimeout(()=>{this.progress = "0%";}, 1000);
        this.uploadStatus = "completely uploaded";
      }
}, error => {
    console.log(error);
}); 
    //this.form.reset();
    }
  }
  onPhotoUpload(event: Event) {
    const file = (event.target as HTMLInputElement).files[0];
    this.form.patchValue({photo1: file});
    this.form.get('photo1').updateValueAndValidity();
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = () => {
      this.imgPreview1 = reader.result;
    };
  }
  onPhotoUpload2(event: Event) {
    const file = (event.target as HTMLInputElement).files[0];
    this.form.patchValue({photo2: file});
    this.form.get('photo2').updateValueAndValidity();
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = () => {
      this.imgPreview2 = reader.result;
    };
  }
  onPhotoUpload3(event: Event) {
    const file = (event.target as HTMLInputElement).files[0];
    this.form.patchValue({photo3: file});
    this.form.get('photo3').updateValueAndValidity();
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = () => {
      this.imgPreview3 = reader.result;
    };
  }
  empty(num) {
    const img = 'photo'+num;
    const imgPrev = 'imgPreview'+num;
    this.form.get(img).reset();
    this[imgPrev] = null;
  }
}
