import { Injectable } from '@angular/core';
import { HttpClient, HttpRequest } from '@angular/common/http';
import { Router } from '@angular/router';
import { Subject, of } from 'rxjs';
// import { ProgressHttp } from 'angular-progress-http';
import { environment } from 'src/environments/environment.prod';
const url = environment.Server_Url+'admin/';

@Injectable({providedIn: 'root'})
export class AdminServices {
    updatedItems= new Subject<{product: Object}>();
    constructor(private http: HttpClient, private router: Router){}
    updatedItemsListener() {
        return this.updatedItems.asObservable();
    }
    uploadItem(post) {
        const postData = new FormData();
        postData.append('brand', post.brand);
        postData.append('color', post.color);
        postData.append('gender', post.gender);
        postData.append('images', post.photo1, post.brand);
        if(post.photo2){postData.append('images', post.photo2, post.brand);}
        if(post.photo3){postData.append('images', post.photo3, post.brand);}
        postData.append('type', post.type);
        postData.append('design', post.design);
        postData.append('quantity', post.quantity);
        postData.append('oldPrice', post.oldPrice);
        postData.append('newPrice', post.newPrice);
        postData.append('size', post.size);
        postData.append('description', post.description);
        const req = new HttpRequest('POST', url+'products/upload', postData, {
            reportProgress: true,
          });

        return this.http.request(req);
    }
    editItem(post) {
        let postData;
        postData = post;
        if((typeof post.photo1 === 'object') || (typeof post.photo2 === 'object')|| (typeof post.photo3 === 'object')) {
        postData = new FormData();
        postData.append('id', post.id);
        postData.append('brand', post.brand);
        postData.append('color', post.color);
        postData.append('gender', post.gender);
        if(post.photo1 && typeof post.photo1 === 'string'){postData.append('photo1', post.photo1);}
        if(post.photo2 && typeof post.photo2 === 'string'){postData.append('photo2', post.photo2);}
        if(post.photo3 && typeof post.photo3 === 'string'){postData.append('photo3', post.photo3);}
        if(post.photo1){postData.append('images', post.photo1);}
        if(post.photo2){postData.append('images', post.photo2);}
        if(post.photo3){postData.append('images', post.photo3);}
        postData.append('type', post.type);
        postData.append('design', post.design);
        postData.append('quantity', post.quantity);
        postData.append('oldPrice', post.oldPrice);
        postData.append('newPrice', post.newPrice);
        postData.append('size', post.size);
        postData.append('description', post.description);
         }
        const req = new HttpRequest('PUT', url+'products/edit', postData, {
            reportProgress: true,
          });

        return this.http.request(req);
    } 
    getProducts(pg, limit) {
        const skip = limit*pg - limit;
        return this.http.get<{items: Object, count: Number}>(url+"products/getAll/"+skip+"/"+limit);
    }
    deleteItem(id) {
        return this.http.delete<{del: Boolean}>(url+"products/delete/" + id);
    }
    getOne(id) {
        return this.http.get<{item: Object}>(url+"products/getOne/"+id);
    }
    searchquery(q) {
        return this.http.get<{items: Object[]}>(url+'products/search/'+q);
    }
    getUsers(pg, limit) {
        const skip = limit*pg - limit;
        return this.http.get<{users: Object[], count: number}>(url+'users/getAll/'+skip+"/"+limit);
        }
    deleteUser(id) {
        return this.http.delete<{del: Boolean}>(url+"users/delete/" + id);
    }
    getOneUser(id) {
    return this.http.get<{user: Object}>(url+"users/getOne/"+id);
    }
    searchqueryUsers(q) {
    return this.http.get<{users: Object[]}>(url+'users/search/'+q);
    }
    updateRole(id) {
        const body = {id};
        return this.http.post<{users: Object[]}>(url+'users/role', body);
    }
} 
