import { environment } from './../../environments/environment.prod';
import { Subject } from 'rxjs';
import { Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import {  Injectable } from '@angular/core';
import {  AuthData } from './auth-data.model';
const serverUrl = environment.Server_Url + 'users';

@Injectable({providedIn: 'root'})
export class UsersServices {
  constructor(private http: HttpClient, private router: Router) {}
  private isAuthenticated = false;
  isAdminAuthenticated = false;
  private token: any;
  private expireTime: any;
  private userId: string;
  authSubject = new Subject<boolean>();
  adminSubject = new Subject<boolean>();
  countdown = new Subject<number>();
  username: string;
  password: string;

  authStatListener() {
    return this.authSubject.asObservable();
  }
  adminStatListener() {
    return this.adminSubject.asObservable();
  }
  countdownListener() {
    return this.countdown.asObservable();
  }
  getAuthStatus() {
    return this.isAuthenticated;
  }
  getAdminStatus() {
    return this.isAdminAuthenticated;
  }
   checkUsername(username) {
    return this.http.get<{found: Boolean}>(serverUrl+'/check/'+username);
  }
  add(username: string, password: string) {
    const data: AuthData = {username, password};
    this.http.post<{message: string, data: any}>(serverUrl + '/add', data).subscribe(res => {
      if (res.data) {
      this.login(res.data.username, res.data.password,0);
      }
    });
  }
  login(username: string, password: string, expireTimems: any) {
    const data = {username, password, expireTimems};
    this.http.post<{message: string, token: object, userId: string, expireTime: number, role: Number}>
    (serverUrl + '/login', data).subscribe(res => {
      console.log(res.message);
      if (res.token) {
        if(res.role===1){this.isAdminAuthenticated = true;this.adminSubject.next(true);}
        else{this.isAuthenticated = true;this.authSubject.next(true);
        }
        this.token = res.token;
        this.username = username;
        this.password = password;
        console.log(res.expireTime);
        this.setexpireTimeout(res.expireTime);
        this.userId = res.userId;
        const now = new Date();
        const expireDate = new Date(now.getTime() + res.expireTime * 1000);
        this.saveAuthData(expireDate);
        this.router.navigate(['/']);
        }
      });
    }
    logout() {
      this.token = null;
      this.isAuthenticated = false;
      this.isAdminAuthenticated = false;
      this.authSubject.next(false);
      this.adminSubject.next(false);
      this.userId = null;
      clearTimeout(this.expireTime);
      this.clearAuthData();
      this.router.navigate(['/login']);
    }
    getToken() {
      return this.token;
    }
    getUserId() {
      return this.userId;
    }
    private setexpireTimeout(expTime) {
      this.expireTime = setTimeout(() => { this.logout(); }, expTime * 1000);
    }
    private saveAuthData(expDate) {
      //localStorage.setItem('token', this.token);
      localStorage.setItem('userId', this.userId);
      localStorage.setItem('username', this.username);
      localStorage.setItem('password', this.password);
      localStorage.setItem('expireDate', expDate.toISOString());
    }
    private clearAuthData() {
      //localStorage.removeItem('token');
      localStorage.removeItem('username');
      localStorage.removeItem('password');
      localStorage.removeItem('expireDate');
      localStorage.removeItem('userId');
    }
    private getAuthData() {
      //const token = localStorage.getItem('token');
      const exp = localStorage.getItem('expireDate');
      const userId = localStorage.getItem('userId');
      const username = localStorage.getItem('username');
      const password = localStorage.getItem('password');
      if (!username || !password || !exp || !userId ) {
        return;
      }
      const token = this.token;
      return {token, expire: new Date(exp), userId, username, password };
    }
    autoAuthCheck() {
      const expireData = this.getAuthData();
      if (!expireData) {
        return;
      }
      const now = new Date();
      const timeLeft = expireData.expire.getTime() - now.getTime();
      if (timeLeft > 0) {
        console.log(timeLeft/1000);
        this.token = expireData.token;
        this.setexpireTimeout(timeLeft / 1000);
        this.login(expireData.username, expireData.password, timeLeft/1000);
        this.isAuthenticated = true;
        this.userId = expireData.userId;
        this.authSubject.next(true);
      }
    }
    countDown() {
      const expireData = this.getAuthData();
      if (!expireData) {
        return;
      }
      const now = new Date();
      const timeLeft = expireData.expire.getTime() - now.getTime();
      if (timeLeft > 0) {
        let intervals = Math.floor(timeLeft / 1000);
        const setinterval = setInterval(() => {
          --intervals;
          if (intervals === 0) {
            clearInterval(setinterval);
          }
          this.countdown.next(intervals);
        }, 1000);
      }
    }
    getCount() {
      const expireData = this.getAuthData();
      if (!expireData) {
        return;
      }
      const now = new Date();
      const timeLeft = expireData.expire.getTime() - now.getTime();
      return Math.floor(timeLeft);
    }
  }
